import { format } from 'date-fns';

/**
 * Get current selected shipping method for a package.
 * @param int package_id Package ID.
 * @returns 
 */
export function getSelectedShippingMethod ( package_id = 0 ) { 
	let shippingRates = wp.data.select( 'wc/store/cart' ).getShippingRates();
	
	if ( !shippingRates|| !shippingRates[ package_id ] ) {
		return false;
	}

	let selectedShipping = shippingRates[ package_id ].shipping_rates.find( ( rate ) => rate.selected === true );

	return selectedShipping?.rate_id ? selectedShipping.rate_id : false;
}

/**
 * Get labels.
 * 
 * @param string key Label Id/key.
 *
 * @returns string
 */
export function getLabel( key ) {
	if ( ! wcSettings[ 'iconic-wds_data' ] || ! wcSettings[ 'iconic-wds_data' ]?.labels ) {
		return '';
	}

	return wcSettings[ 'iconic-wds_data' ].labels[ key ];
}

/**
 * Get any setting.
 * 
 * @param string WPSF Setting key/ID.
 * @returns 
 */
export function getSetting( key = '' ) {
	if ( !wcSettings[ 'iconic-wds_data' ] || !wcSettings[ 'iconic-wds_data' ].settings ) { 
		return '';
	}

	if ( key ) {
		return wcSettings[ 'iconic-wds_data' ].settings[ key ];
	}
	
	return wcSettings[ 'iconic-wds_data' ].settings;
}

/**
 * When timeslot is changed then the fees should be updated.
 * This function updates the cart which forces the fees to be recalculated.
 *
 * @returns 
 */
export function updateCart() {
	const { extensionCartUpdate } = wc.blocksCheckout;

	let extensionData = wp.data.select( 'wc/store/checkout' ).getExtensionData();

	if ( ! extensionData || ! extensionData[ 'iconic-wds' ] ) {
		return;
	}

	extensionCartUpdate( {
		namespace: 'iconic-wds',
		data: {
			date: extensionData[ 'iconic-wds' ].date,
			date_ymd: extensionData[ 'iconic-wds' ].date_ymd,
			timeslot: extensionData[ 'iconic-wds' ].timeslot,
			shipping_method: getSelectedShippingMethod(0)
		},
	} );
}

/**
 * Is timeslot enabled?
 * @returns bool
 */
export function isTimeslotEnabled() {
	return '1' === window.wcSettings[ 'iconic-wds_data' ]?.settings[ 'timesettings_timesettings_setup_enable' ];
}

/**
 * Is auto-select first date setting enabled?
 * @returns bool
 */
export function autoSelectFirstDate () {
	return '1' === window.wcSettings[ 'iconic-wds_data' ]?.settings.datesettings_datesettings_setup_auto_select_first;
}

/**
 * Is auto-select first date setting enabled?
 * @returns bool
 */
export function autoSelectTimeslot () {
	return '1' === window.wcSettings[ 'iconic-wds_data' ]?.settings.timesettings_timesettings_setup_auto_select_first;
}

/**
 * Get the start day of the week.
 * @returns int
 */
export function getCalendarStartDay () {
	let week_last_day = wcSettings[ 'iconic-wds_data' ].settings.datesettings_datesettings_last_day_of_week;

	let map = {
		'sunday': 1,
		'monday': 2,
		'tuesday': 3,
		'wednesday': 4,
		'thursday': 5,
		'friday': 6,
		'saturday': 0,
	};

	return map[ week_last_day ];
}

/**
 * Calculate and return the fee for the given date.
 * @param Date date
 * @returns 
 */
export function getFeeForDate ( date ) {
	let day_number = date.getDay();
	let ymd = format( date, 'yyyyMMdd' );
	let fee = typeof jckwds_vars.day_fees[ ymd ] !== 'undefined'
			? parseFloat( jckwds_vars.day_fees[ ymd_stripped ] )
		: parseFloat( jckwds_vars.day_fees[ day_number ] );
	
	let same_day_fees = getSetting( 'datesettings_fees_same_day' );
	let next_day_fees = getSetting( 'datesettings_fees_next_day' );
	
	// Same day
	if (
		ymd === jckwds_vars.dates.same_day &&
		same_day_fees > 0
	) {
		fee += parseFloat(
			same_day_fees
		);
	}

	// Next day
	if (
		ymd === jckwds_vars.dates.next_day &&
		next_day_fees > 0
	) {
		fee += parseFloat(
			next_day_fees
		);
	}

	return isNaN( fee ) ? false : accounting.formatMoney( fee, jckwds_vars.currency );
}
