import { __ } from '@wordpress/i18n';
import { useBlockProps, RichText } from '@wordpress/block-editor';
import { Disabled } from '@wordpress/components';
import { getSetting as getWcSettings } from '@woocommerce/settings';
import DatePicker from "react-datepicker";
import { getLabel, getSetting as getWdsSetting, isTimeslotEnabled } from '../helper';

export const Edit = ({ attributes, setAttributes }) => {
	const blockProps = useBlockProps();
	const datePickerTheme = getWdsSetting( 'datesettings_datesettings_setup_uitheme' );

	return (
		<div {...blockProps} style={{ display: 'block' }}>
			<div className={"wp-block-iconic-wds wp-block-iconic-wds--theme-" + datePickerTheme}>
				<div>
					<Disabled>
						<div className='iconic-wds-block__row iconic-wds-block__date'>
							<label>{getLabel('date')}</label>
							<DatePicker />
						</div>
						{isTimeslotEnabled() &&
							<div className='iconic-wds-block__row iconic-wds-block__time'>
								<label>{getLabel( 'time_slot' )}</label>
								<select>
									<option>{getLabel( 'select_date_first' )}</option>
								</select>
							</div>
						}
					</Disabled>
				</div>
			</div>
		</div>
	);
};
